import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AppFacade } from 'src/app/store/app.facade';
import { isMobile } from 'src/app/utils';

@Component({
  selector: 'app-resume',
  templateUrl: './resume.component.html',
  styleUrls: ['./resume.component.scss']
})
export class ResumeComponent implements OnInit {

  // For open resume in mobile version
  private lang: string;
  private resume: string;

  constructor(
    public appFacade: AppFacade,
    private router: Router,
    private translate: TranslateService
  ) { }

  ngOnInit(): void {
    this.lang = this.translate.currentLang;
    this.appFacade.setItIsHome(false);
    this.getResumeByLanguage();
  }

  public open() {
    this.appFacade.sendAnalyticsEvent('open_resume', 'menu', this.lang, 'click');
  }

  private getResumeByLanguage() {
    switch (this.lang) {
      case 'es':
        this.resume = 'assets/docs/JeisonNisperuzaES.pdf';
        break;
      case 'en':
        this.resume = 'assets/docs/JeisonNisperuzaEN.pdf';
        break;
      default:
        this.resume = 'assets/docs/JeisonNisperuzaES.pdf';
        break;
    }
  }

}
